<template>
  <div class="reader" ref="reader">
    <div class="reader-top" :class="{active:active==1}">
      <i class="icon-fanhui" @click="back"></i>
      <div class=reader-top-more>
        <i class="icon-gengduo-tianchong" @click="isLookMore=!isLookMore"></i>
        <ul v-show="isLookMore" class="more-nav">
          <li class="more-nav-item"><i class="icon-shujia"></i>去书架</li>
          <li class="more-nav-item"><i class="icon-book_icon"></i>书籍详情</li>
          <li class="more-nav-item"><i class="icon-fenxiang"></i>分享此书</li>
        </ul>
      </div>
    </div>
    <div class="bg" ref="bg"></div>
    <div class="content" v-html="content" ref="content" @click="select"></div>
    <div class="page-btn">
      <span class="page-btn-prev" :class="{wrong:index==0}" @click="changePage(-1)">上一章</span>
      <span class="page-btn-next" :class="{wrong:index>=catalog.length-1}" @click="changePage(1)">下一章</span>
    </div>
    <div class="reader-bottom">
      <ul :class="{active:active==1}" class="reader-bottom-nav">
        <li class="reader-bottom-nav-item" @click="changeActive(2)"><i class="icon-sort"></i>目录</li>
        <li class="reader-bottom-nav-item" @click="changeActive(3)"><i class="icon-liangdu"></i>亮度</li>
        <li class="reader-bottom-nav-item" @click="changeActive(4)"><i class="icon-shezhi"></i>设置</li>
        <li class="reader-bottom-nav-item" @click="changeActive(5)"><i class="icon-pinglun"></i>评论</li>
      </ul>
      <ul class="reader-bottom-content">
        <li class="reader-bottom-content-cata" :class="{active:active==2}">
          <div class="reader-bottom-content-cata-title">
            <h3 class="title">{{book_name}}</h3>
            <h4 class="catalog-num">共{{catalogLength}}</h4>
          </div>
          <ul class="catalog-list-wrap">
            <li class="catalog-list" :class="{active:item.chapid==chapId}" v-for="item in catalog" :key="item.chapid">
              {{item.chap_name}}
            </li>
          </ul>
        </li>
        <li class="reader-bottom-content-sun" :class="{active:active==3}">
          <i class="icon-liangdu icon-l"></i>
          <i class="icon-liangdu icon-r"></i>
          <div class="ld-line" @click="changeLD" ref="ldBg">
            <p class="ld-adjust" ref="ldLine">
            </p>
          </div>
        </li>
        <li class="reader-bottom-content-set" :class="{active:active==4}"></li>
        <li class="reader-bottom-content-comment" :class="{active:active==5}"></li>
      </ul>
    </div>
  </div>
</template>

<script>
  import {getChap} from "api/bookreader"
  import {mapGetters, mapMutations} from "vuex"
  import {ERR_OK} from "common/js/config"
  import {getCatalog} from "api/bookintro"
  import {getLight, setLight} from "common/js/storage"

  export default {
    name: 'reader',
    created() {
      if (!this.bookId) {
        this.$router.back();
        return
      }
      this._getChap();
      this._getCatalog();
      this.$nextTick(() => {
        let n = getLight();
        this.domAction(n);
      })
    },
    methods: {
      back() {
        this.$router.back();
      },
      domAction(n) {
        let w = this.$refs.ldBg.offsetWidth;
        this.$refs.ldLine.style.width = n / .75 * w + "px";
        this.$refs.bg.style.opacity = n;
      },
      changePage(num) {
        this.index = this.index + num;
        if (this.index < 0) {
          this.index = 0;
          return;
        } else if (this.index >= this.catalog.length) {
          this.index = this.catalog.length - 1;
          return;
        }
        this.set_chapId(this.catalog[this.index].chapid);
        this.content = "";
        this._getChap();
        this.$refs.reader.scrollTop = 0;
      },
      _getChap() {
        getChap(this.bookId, this.chapId).then(res => {
          if (res.status == ERR_OK) {
            let data = res.data.data.content;
            data = data.replace(/\n/g, "</p><p>");
            data = data.slice(0, -1);
            data = "<p>" + data + "</p>";
            this.content = data;
          }
        })
      },
      _getCatalog() {
        getCatalog(this.bookId).then(res => {
          if (res.status == ERR_OK) {
            this.catalog = [];
            this.book_name = res.data.main.book_name;
            let data = res.data.data[0].volumeList.chapters;
            data.forEach((item, index) => {
              if (this.chapId == item.chapid) {
                this.index = index
              }
              this.catalog = this.catalog.concat(item);
            })

          }
        })
      },
      select() {
        if (this.active != 0) {
          this.active = 0;
          return;
        }
        this.active = 1;
      },
      changeActive(num) {
        this.active = num;
        this.$refs.bg.style.opacity = getLight();
      },
      changeLD(e) {
        let w = this.$refs.ldBg.offsetWidth,
          curL = e.clientX - this.$refs.ldBg.offsetLeft,
          n = curL / w * .75;
        this.domAction(n);
        setLight(n)
      },
      ...mapMutations({
        "set_chapId": "SET_CHAPID"
      })
    },
    computed: {
      catalogLength() {
        return this.catalog.length;
      },
      ...mapGetters([
        "chapId",
        "bookId"
      ])
    },
    data() {
      return {
        isLookMore: false,
        content: "",
        catalog: [],
        beginCatalog: "",
        book_name: "",
        active: 0,
        index: 0,
      }
    }
  }
</script>

<style lang="less" type="text/less">
  @import "~common/less/variable";
  @import "~common/less/mixin";

  .transform {
    transition: all .3s ease-in;
    opacity: 0.3;
  }

  .active {
    transform: translateY(0) translateX(0);
    opacity: 1;
  }

  .active2 {
    transform: translateY(-100%);
    opacity: 1
  }

  .reader {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 6000;
    background-color: @color-bg-th;
    overflow: scroll;

    .reader-top {
      position: fixed;
      left: 0;
      top: 0;
      height: .6rem;
      width: 100%;
      background-color: @color-bg-gray2;
      z-index: 60;
      transform: translateY(-100%);
      .transform;
      &.active {
        .active;
      }
      .icon-fanhui {
        position: absolute;
        left: .1rem;
        top: 0;
        line-height: .6rem;
      }
      .reader-top-more {
        position: absolute;
        right: .2rem;
        top: 0;

        .icon-gengduo-tianchong {
          position: absolute;
          right: 0;
          top: 0;
          line-height: .6rem;
        }
        .more-nav {
          background-color: @color-bg-white;
          width: 1.3rem;
          position: absolute;
          right: 0;
          top: .6rem;
          font-size: @font-size-s;
          white-space: nowrap;
          line-height: 2;
          border: 1px solid @color-line;
          .more-nav-item {
            position: relative;
            padding-left: .4rem;
            user-select: none;

            i {
              position: absolute;
              left: .1rem;
              top: 0;
            }
          }

        }
      }
    }
    .reader-bottom {
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 0;
      z-index: 55;
      .reader-bottom-nav {
        background-color: @color-bg-gray2;
        height: .97rem;
        .transform;
        display: flex;
        flex-flow: row nowrap;
        font-size: @font-size-s;
        &.active {
          .active2
        }
        .reader-bottom-nav-item {
          flex: 1;
          text-align: center;
          padding-top: .6rem;
          position: relative;
          i {
            font-size: @font-size-l;
            position: absolute;
            margin-left: -@font-size-l/2;
            left: 50%;
            top: .25rem;
          }
        }
      }
      .reader-bottom-content {
        position: absolute;
        bottom: 0;
        left: 0;
        .reader-bottom-content-cata {
          position: fixed;
          width: 90%;
          z-index: 222;
          top: 0;
          bottom: 0;
          overflow: auto;
          box-shadow: 8px 8px 30px @color-theme-font;
          background-color: @color-bg-th;
          line-height: 1.5;
          padding: .2rem;
          box-sizing: border-box;
          transform: translateX(-100%);
          .transform;
          &.active {
            .active;
          }
          .reader-bottom-content-cata-title {
            position: fixed;
            top: 0;
            left: 0;
            padding: .2rem;
            width: 90%;
            box-sizing: border-box;
            background-color: @color-bg-th;
            .title {
              font-size: @font-size-m;
              .nowrap;
            }
            .catalog-num {
              font-size: @font-size-s;
            }

          }
          .catalog-list-wrap {
            padding: .3rem 0;
            padding-top: .9rem;
            .catalog-list {
              font-size: @font-size-m;
              .nowrap;
              &.active {
                color: @color-text-danger;
              }
            }
          }

        }
        .reader-bottom-content-sun {
          background-color: @color-bg-gray2;
          position: fixed;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 1rem;
          transform: translateY(100%);
          .transform;
          &.active {
            .active;
          }
          .icon-liangdu {
            position: absolute;
            font-size: @font-size-l;
            top: 50%;
            margin-top: -@font-size-l/2;
            &.icon-l {
              left: .15rem;
            }
            &.icon-r {
              right: .15rem;
              color: @color-theme;
            }
          }
          .ld-line {
            margin: .45rem .7rem;
            background: @color-font-gray;
            border-radius: .1rem;
            height: .1rem;
            position: relative;
            .ld-adjust {
              width: 0;
              background-color: @color-theme;
              border-radius: .1rem;
              height: .1rem;
              position: relative;
              &::before, &::after {
                content: "";
                position: absolute;
                right: 0;
                top: 50%;
                width: .2rem;
                height: .2rem;
                border-radius: 50%;
                transform: translateY(-50%) translateX(50%);
              }
              &::before {
                background-color: @color-bg-white;
              }
              &::after {
                background-color: @color-theme;
                width: .1rem;
                height: .1rem;

              }

            }
          }

        }
      }
    }
    .content {

      padding: .1rem;
      position: relative;
      z-index: 55;

      p {
        text-indent: 2em;
        font-size: @font-size-s;
        line-height: 1.5;
        margin: .1rem 0;
      }
    }
    .bg {
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      background-color: #000;
      z-index: -1;
      opacity: 0;
    }
    .page-btn {
      font-size: @font-size-s;
      text-align: center;
      .page-btn-prev, .page-btn-next {
        display: inline-block;
        width: 40%;
        line-height: 2;
        cursor: pointer;
        color: @color-theme;
        border-radius: .2rem;
        border: 1px solid @color-theme;
        margin-bottom: .15rem;

      }
      .wrong {
        color: @color-font-gray;
        border-color: @color-font-gray;
      }
    }
  }
</style>



